
/* 핀 */
.pin{
   position: relative;
   top: calc(50% - 11px);
   left: calc(50% - 11px);
   filter: drop-shadow(1px 1px 6px rgba(0, 0, 0, 0.3));
}
.pin img {
  width: 22px;
  height: 22px;
}

/* 여기 부터 남의 핀컴 */
.comcontain{
    z-index: 2;
    position: absolute;
    filter: drop-shadow(3px 3px 13px rgba(0, 0, 0, 0.25));
    width: 234px;
    overflow-x: visible;
}

/* 말풍선 위치 */

.ct1 {
  margin: 5.8vh 0 0 12.4vw;
}

.ct2 {
  margin: 5.8vh 0 0 -47.4vw;
}

.ct3 {
   margin: -15vh 0 0 12.4vw;
}

.more2L{
    /* 두줄이상 */
    margin-top: -17vh;
}

.combox1 {
    position: relative;
    background-color: rgba(33, 33, 44, 0.9);
    line-height: 120%;
  }

.combox2 {
    position: relative;
    line-height: 120%;
    background:rgba(252, 252, 252, 0.9);
    color:#21212C;
}

/* 박스 모퉁이 */

.cb1 {
    border-radius: 0px 8px 8px 8px;
}
.cb2 {
    border-radius: 8px 0px 8px 8px;
}
.cb3 {
    border-radius: 8px 8px 8px 0px;
}
.cb4 {
    border-radius: 8px 8px 0px 8px;
}

.tail {
    width: 14px;
    height: 9px;
    z-index: 10;
    position: relative;
    margin-top: -3px;
}

 /* 말꼬리 위치 - 포지션 릴레이티브되어서 밑에 다 바꿔야함*/
.t1{ 
    margin-top: -58.2%;
    transform: rotate(180deg);
}

.t2{ 
    margin-top: -58.2%;
    margin-left: auto;
    transform: scaleX(-1) rotate(180deg);
}

.t3{ 
    top: 100%;
    transform: scaleX(-1);
}

.t4{ 
    margin-left: auto;
}


/* 탑바 */
.topbar{
    height: 2px;
    width: calc(100% - 17px);
    margin: 0 auto 20px;
    font-size: 0;
    overflow-x: visible;
}

/* 닷 */

.dots_custom {
    z-index: 10;
    display: inline-block;
    height: 2px;
    width: calc(100% - 17px);
    margin: 0 8px 20px;
    font-size: 0;
    overflow-x: visible;
  }
  .dots_custom ul {
    padding:0;
    margin: 0;
    display: flex;
  }
  
  .dots_custom li {
    flex: 1;
    cursor: pointer;
    display: inline-block;
    padding: 0;
    margin-right: 1px;
  }
  
  .dots_custom li button {
    background-color: #21212C;
    opacity: 0.1;
    border-radius: 10px;
    height: 2px;
    width: 100%;
  }
  
  .dots_custom li.slick-active button {
    opacity: 0.4;
  }

  .dot1 li button {
    background: #fcfcfc;
  }


/* 텍스트 박스 */
.txtBox {
    margin: 0 16px;
}

/* 프로필 */
.prof1 {
    display: flex;
    padding: 0;
    margin: 0;
    line-height: 16px;
    justify-content: space-between;
}
.prof1 li{
    display: inline-block;
    margin-right: 4px;
}

.profImgs{
    width: 16px;  
}
.nam1 {
    font-size: 12px;
    font-weight: 600;
    opacity: 0.4;
}

.num1{
    font-size: 12px;
    font-weight: 500;
    opacity: 0.4;
}

/* 하트 */
.like div {
    display: inline-block;
}

/* 댓글 내용 */

.com1 {
    display: block;
    margin:8px 0 8px;
    font-style: Medium;
    line-height: 150%;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.cts {
    margin-bottom: 29px;

}

/* 대댓글 링크댓글 */

.reply1 {
    padding: 0;
    margin: 0 16px;
    border-top: 1px solid rgba(252, 252, 252, 0.1);
}
.reply2{
    border-top: 1px solid rgba(33, 33, 44, 0.06);
}

.reply1 li{
    display: inline-block;
    width: 50%;
    font-size: 12px;
    font-weight: 600;
    text-align: center;
    margin: 12px auto;
   color: rgba(252, 252, 252, 0.4);
}

.reply1 .repli {
    color:rgba(33, 33, 44, 0.4);
}

.reply1 li img{
    display: inline-block;
    width: 16px;
    margin-right: 4px;
}

.reply1 li:first-child{
    border-right: 1px solid rgba(252, 252, 252, 0.1);
}
.reply1 .repli:first-child{
    border-right: 1px solid rgba(33, 33, 44, 0.06);

}