* {
    box-sizing: border-box;
    overflow-x: hidden;  
  }

  input {
    all: unset;
    box-sizing: border-box;
    appearance: none;
  }

  button {
    all: unset;
    box-sizing: border-box;
    appearance: none;
  }
  
  /* 스크롤바 없애기 */
  ::-webkit-scrollbar {
    display: none;
  }

  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 14px;
    -ms-overflow-style: none;
    color: #fcfcfc;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }

  .app{
    position: relative;
    width: 100%;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }

  li {
    list-style:none;
  }

  img {
    width: 100%;
    vertical-align: top;
  }
  