.toonimg{
  width: 100%;
  font-size: 0;
  position: absolute;
  z-index: -2;
}

.grid {
  z-index: -1;
  position: absolute;
  top: 0;
  width: 100%;
  background-image: url('./grid.png');
  background-repeat: repeat-y;
  background-size: 100vw;
}

.pincoms_gridbox {
  position: absolute;
  z-index: -1;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 25vw;
}

/* 상단 바 */

.topNav {
  display: inline-block;
  position: fixed;
  top: 0;
  background: rgba(33, 33, 44, 0.9);
  width: 100%;
  height: 55px;
  padding: 0;
  margin: 0;

}
.topNav ul{
  padding: 0;
  margin: 0;
  margin-left: 20px;
}
.topNav img {
  margin-top: 16px;
}
.topNav li {
  display: inline-block;
  margin-right: 16px;
  line-height: 55px;
  z-index: 10;
}

.topNav div {
  float: right;
  width: 24px;
  margin-right: 20px;
}

/* 바텀 네비 */
.bottomNavi {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  background: rgba(33, 33, 44, 0.9);
  text-align: center;
}
.bottomNavi li{
  display: inline-block;
  width: 78px;
}

/* 하단 플로팅 버튼들 */

.floating {
  position: fixed;
  left: calc(50% - 102.5px);
  bottom: 80px;
  filter: drop-shadow( 2px 2px 8px rgba(33, 33, 44, 0.25));
}

.floating li {
  display: inline-block;
  margin-right: 8px;
  overflow: hidden;
  touch-action: none;
}

.togg{
  width: 45px;
}

.plus{
  margin-left: 3px;
  width: 45px;
}

/* 핀모드 활성화 모드 */

.pinon{
  width: 91px;
  height: 43px;
  background: #454CE7;
  border-radius: 50px;
  margin: 3px;
  box-sizing:unset;
}

.pinon img {
  width: 57px;
  vertical-align: center;
  margin: 13px 17px;
}

@property --rotate {
  syntax: "<angle>";
  initial-value: 132deg;
  inherits: false;
}

.pinon::before {
  content: "";
  width: 97px;
  height: 49px;
  border-radius: 50px;
  background-image: linear-gradient(
    var(--rotate)
    , #787EF9, #4CDCC2);
    position: absolute;
    z-index: -1;
    margin: -3px 0 0 -3px;
    animation: spin 4s linear infinite;
}

@keyframes spin {
  0% {
    --rotate: 0deg;
  }
  100% {
    --rotate: 360deg;
  }
}
